.Youtube {
	> .inner {
		max-width: 100%;
		background: #f5f5f5;

		.wrap {
			position: relative;
			width: 100%;
			max-width: 1280px;
			margin: 0 auto;
			padding: 70px 0;
			&::before {
				content: '';
				display: block;
				width: 80%;
				height: 100%;
				background: #fff;
				position: absolute;
				top: 0;
				left: 0;
			}
			&::after {
				content: 'YOUTUBE';
				display: block;
				position: absolute;
				bottom: 0;
				left: 0;
				font-size: 15em;
				font-weight: bold;
				color: #f5f5f5;
				transform: rotate(-90deg) translate(-20%, -20%);
				transform-origin: 0 0;
				line-height: 1;
			}

			article {
				position: relative;
				z-index: 2;
				width: 100%;
				margin-bottom: 50px;
				.inner {
					width: 100%;
					position: relative;
					display: flex;
					justify-content: flex-end;
					.pic {
						width: 50%;
						height: 250px;
						box-shadow: 5px 5px 5px rgb(0 0 0 / 20%);
						img {
							width: 100%;
							height: 100%;
							object-fit: cover;
							filter: grayscale(0.8);
						}
					}

					.txt {
						position: absolute;
						width: 40%;
						height: 160px;
						padding: 40px 50px;
						top: 50%;
						left: 0;
						background: rgb(245, 245, 245);
						transform: translate(50%, -50%);
						h2 {
							font: 500 16px/1 'Oswald';
							text-transform: uppercase;
							color: #212121;
							margin-bottom: 15px;
						}
						p {
							font-size: 14px;
							color: #999;
						}
						svg {
							font-size: 22px;
							color: #272727;
							cursor: pointer;
							position: absolute;
							top: 50%;
							right: 40px;
							transform: translateY(-50%);
						}
					}
				}
				&:nth-of-type(2n) {
					.inner {
						.txt {
							background: rgb(198, 164, 126);
							h2 {
								color: #fff;
							}
							p {
								color: #f5f5f5;
							}
							svg {
								color: #fff;
							}
						}
					}
				}
			}
		}
	}
}
@media screen and (max-width: $web_s) {
	.Youtube {
		> .inner {
			.wrap {
				padding: 70px 5vw;
				article .inner .txt {
					padding-right: 70px;
					p {
						word-break: break-all;
					}
				}
			}
		}
	}
}
@media screen and (max-width: $tablet) {
	.Youtube {
		> .inner {
			.wrap {
				article {
					.inner {
						.pic {
							width: 75%;
						}
						.txt {
							width: 75%;
							height: 100%;
							left: 25%;
							top: 0;
							transform: translate(0, 0);
							background: rgb(245, 245, 245, 0.7);
							p {
								color: #666;
							}
							svg {
								position: relative;
								top: auto;
								right: auto;
								transform: translateY(0%);
								margin-top: 20px;
							}
						}
					}
					&:nth-of-type(2n) {
						.inner {
							.txt {
								background: rgb(198, 164, 126, 0.7);
							}
						}
					}
				}
			}
		}
	}
}
@media screen and (max-width: $mo) {
	.Youtube {
		> .inner {
			.wrap {
				&::after {
					display: none;
				}
				article {
					.inner {
						.pic {
							width: 100%;
						}
						.txt {
							width: 100%;
							left: 0%;
							top: 0;
						}
					}
				}
			}
		}
	}
}

@media screen and (max-width: $mo_s) {
	.Youtube {
		> .inner {
			.wrap article .inner .pic {
				height: 180px;
			}
		}
	}
}
